(function () {

    const persistir = config.hasOwnProperty('salvarDados') ? config.salvarDados : true;
    const api = persistir ? PersistanceService.Create() : new PersistanceFake();

    window.scorm = api;
    window.bridge = new EventEmitter();
    window.view = new ViewController(document.querySelector('#main'), config, api);

    if (config.debug) Debuggy(window.view);

})();