const QUIZES = "quizes";
const STATUS = {
    PASSED: "passed",
    COMPLETED: "completed",
    FAILED: "failed",
    INCOMPLETE: "incomplete",
    BROWSED: "browsed",
    NOT_ATTEMPTED: "not attempted"
};

const SCORM_API = {
    SUSPEND_DATA: "cmi.suspend_data",
    LESSON_STATUS: "cmi.core.lesson_status",
    LESSON_LOCATION: "cmi.core.lesson_location",
    SCORE: "cmi.core.score.raw"
};

